@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@700&display=swap);
body {
  margin: 0px;
  scroll-behavior: smooth;
}
h1 {
  margin: 0;
}

/*------------------------------------------------------------------------------*/
#root {
  margin: 0 3%;
  min-width: 365px;
  padding: 10px;
  border-radius: 38px;
  background-color: hsl(82, 30%, 35%);
}

@media screen and (max-width: 725px) {
  #root {
    margin: 0 2px;
    padding: 8px;
  }
  header {
    height: 410px;
  }
}

.hr_line {
  width: 90%;
  margin: 10px auto;
  border: 1px solid black;
  box-shadow: 0 0 10px white;
}

header {
  width: 100%;
  height: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media screen and (max-width: 725px) {
  .page_content {
    margin: 0 2px;
    padding: 8px;
  }
  header {
    height: 410px;
  }
}
/*---------------------------------------------------*/
.header_floats {
  width: 49.3%;
  height: 200px;
  background-color: hsla(48, 100%, 93%, 1);
}

@media screen and (max-width: 725px) {
  .header_floats {
    width: 100%;
  }
}

.logo_block {
  border-radius: 30px 0 0 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0;
}
@media screen and (max-width: 725px) {
  .logo_block {
    border-radius: 32px 32px 0 0;
    margin-right: 0;
  }
}

.top-logo_block {
  height: 150px;
  padding: 12px 5px 0;
}

.top-logo_block h1 {
  text-align: center;
  font-size: 36px;
  font-family: "Nanum Myeongjo", serif;
  background-color: crimson;
  color: hsla(48, 100%, 93%, 1);
  padding: 5px;
}
#heading1 {
  margin: 0 auto 0;
  width: 160px;
}
#heading2 {
  margin: 0px auto;
  width: 300px;
  box-shadow: 0 0 20px black;
  animation: skewdown 0.2s ease-in 2s forwards;
}
@keyframes skewdown {
  from {
    margin: 0px auto;
  }
  to {
    transform: rotate(-7deg) skewX(-7deg);
    margin: 10px auto;
  }
}

.bottom-logo_block {
  height: 40px;
  padding: 0 5px 0;
  border-top: 1px solid black;
}
.bottom-logo_block p {
  margin: 6px;
  font-size: 12px;
  text-align: center;
  font-family: sans-serif;
}

/*-------------------------------------------------*/
.info_block {
  border-radius: 0 30px 0 0;
}
@media screen and (max-width: 725px) {
  .info_block {
    margin-left: 0;
    margin-top: 10px;
    border-radius: 0;
  }
}
.top-info_block {
  box-sizing: border-box;
  height: 160px;
  width: 100%;
  padding: 5px;
  text-align: center;
  font-family: "Segoe UI";
}

.bottom-info_block {
  height: 40px;
  width: 100%;
}
.bottom-info_block ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.bottom-info_block li {
  width: 100%;
  height: 40px;
  clear: none;
  flex: 1 1;
}
.nav_btns {
  display: block;
  height: 20px;
  padding: 10px 0px;
  background-color: black;
  color: white;
  font-family: "Segoe UI";
  font-size: 15px;
  text-align: center;
  text-decoration: none;
  transition: all 0.2s linear;
}
.nav_btns:hover {
  color: red;
  background-color: hsla(48, 100%, 93%, 1);
}

footer {
  width: 100%;
  min-height: 100px;
  background-color: hsla(48, 100%, 93%, 1);
  border-radius: 0 0 30px 30px;
  box-sizing: border-box;
  padding: 7px;
}

main {
  box-sizing: border-box;
  width: 100%;
  background-color: hsla(48, 100%, 93%, 1);
  min-height: 90vh;
  max-height: 500px;
  display: grid;
  grid-template-areas: "navbar content";
  grid-template-columns: minmax(200px, 25%) auto;
  grid-column-gap: 0px;
  -webkit-column-gap: 0px;
          column-gap: 0px;
}

.main_content {
  grid-area: content;
  margin: 10px;
  border: 5px solid hsl(82, 30%, 35%);
  text-align: center;
}

nav {
  grid-area: navbar;
  background-color: hsla(48, 100%, 93%, 1);
  padding: 10px;
  max-height: 600px;
  box-sizing: border-box;
}
.searchdiv {
  box-sizing: border-box;
  background-color: hsl(0, 0%, 15%);
  padding: 8px;
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
}

.searchdiv input {
  background-color: hsla(48, 100%, 93%, 1);
  width: 100%;
  height: 100%;
  outline: none;
  padding: 0;
  border-width: 0;
  text-align: center;
}

.navdiv {
  overflow-y: auto;
  background-color: hsl(82, 30%, 35%);
  width: 100%;
  height: calc(100% - 60px);
  color: white;
  text-align: center;
}

